<template>
  <main class="page-container organizers">
    <div class="page-container-body">
      <h1 class="title">Фотоальбом</h1>
      <div class="organizers-container">
        <gallery-item
          v-for="item in events"
          :key="item.id"
          :event="item"
        ></gallery-item>
      </div>
    </div>
  </main>
</template>

<script>
import GalleryItem from '@/components/Student/GalleryItem'
export default {
  components: {
    GalleryItem,
  },
  data() {
    return {
      items: [],
      events: [
        {
          name: 'Победитель Гран-при',
          photos: [],
          id: 1,
        },
        {
          name: 'Церемония награждения',
          photos: [],
          id: 2,
        },
        {
          name: 'Очный этап',
          photos: [],
          id: 3,
        },
        {
          name: 'Хэдлайнеры',
          photos: [],
          id: 4,
        },
        {
          name: 'Организаторы',
          photos: [],
          id: 5,
        },
      ],
    }
  },
  mounted() {
    for (let i = 1; i < 11; i++) {
      this.items.push(`/landing/images/album/${i}.png`)
    }
    this.events = this.events.map(item => {
      item.photos = [...this.items]
      return item
    })
  },
}
</script>

