<template>
  <div class="organize-item">
    <h3>{{ event.name }}</h3>
    <swiper :class="`swiper swiper-${event.id}`" :options="swiperOption">
      <swiper-slide v-for="item in event.photos" :key="item">
        <img :src="item" alt="Фото" />
      </swiper-slide>
      <div
        :class="`swiper-pagination swiper-pagination-${event.id}`"
        slot="pagination"
      ></div>
    </swiper>
    <div class="organize-item--buttons">
      <div :class="`swiper-button-next swiper-button-next-${event.id}`"></div>
      <div :class="`swiper-button-prev swiper-button-prev-${event.id}`"></div>
    </div>
  </div>
</template>

<script>
import 'swiper/css/swiper.css'
import resizeMixin from '@/mixins/resize.mixin'
export default {
  mixins: [resizeMixin],
  props: ['event'],
  data() {
    return {}
  },
  computed: {
    swiperOption() {
      return {
        slidesPerView: 'auto',
        pagination: {
          el: `.swiper-pagination-${this.event.id}`,
          clickable: true,
        },
        navigation: {
          nextEl: `.swiper-button-next-${this.event.id}`,
          prevEl: `.swiper-button-prev-${this.event.id}`,
        },
        breakpoints: {
          '@0.00': {
            slidesPerView: 1,
            spaceBetween: 10,
          },
          '@0.75': {
            slidesPerView: 2,
            spaceBetween: 20,
          },
          '@1.00': {
            slidesPerView: 3,
            spaceBetween: 20,
          },
          '@1.50': {
            slidesPerView: 4,
            spaceBetween: 20,
          },
        },
        keyboard: {
          enabled: true,
        },
        grabCursor: true,
      }
    },
  },
}
</script>

